 * From https://unindented.org/articles/trigonometry-in-sass/
*/
/*
 * Color variables
 */
.Cover {
  height: 90vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center; }

.Cover * {
  text-transform: uppercase; }

.Cover .inner-content .aligned {
  width: 100%; }

.Cover .inner-content .aligned > div {
  display: inline-block;
  margin-bottom: 13vh; }

.Cover .triangles > div:nth-child(1) {
  right: 6.5vh;
  margin-top: 25px; }

.Cover .triangles > div:nth-child(2) {
  left: -2.5vh; }

.Cover .triangles .left,
.Cover .triangles .right {
  border-color: #2D3B20; }

.Cover .triangles .base {
  border-color: #A6B19C; }



